.partners {
    padding: 0 !important;
}

.partners {
    width: 100%;
    display: block;
    position: relative;
    z-index: 1;
}

.partners .partners_s2 {
    height: auto;
}

.partners ul {
    text-align: center;
}

.partners_s2 {
    white-space: nowrap;
    display: inline-block;
    margin: 0;
    padding: 0;
    font-size: 0;
}

.partners_s2 {
    @apply gap-5 w-fit;
    font-size: 0;
    animation: scrollSS 30s linear infinite;
    display: flex;
}

@keyframes scrollSS {

    0% {
        transform: translateX(calc(0%));
    }

    100% {
        transform: translateX(calc(-50%));
    }
}